import { Component, OnInit } from '@angular/core';
import { PaginationService } from 'src/app/service/pagination.service';
import { TextField, ActionField, TextClickEventField } from 'src/app/common-module/table-list/list-field-type';
import { CategoryService } from 'src/app/service/category.service';

@Component({
  selector: 'app-category-list',
  templateUrl: './category-list.component.html'
})
export class CategoryListComponent implements OnInit {

  PageTitle: string = 'Manage Category';
  action: any;
  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  permissionObject: any = null;
  //For Dynamic List
  headerData: Array<any> = [];
  showPagination: boolean = false;
  parentCategoryId: any = false;
  categoryName: any = false;
  parentCategoryCount: any = false;

  constructor(
    private paginationService: PaginationService,
    private categoryService: CategoryService,
  ) { }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['self'];
    // });
    this.loadform = false;
    this.setHeaderData();
    this.getObjects();
  }
  setHeaderData() {
    this.headerData.push(
      new TextField({
        label: "Category name",
        key: "category_name",
      }),
      new TextClickEventField({
        label: "Sub Category Count",
        key: "parent_category_id",
      }),
      new TextField({
        label: "Height(cm)",
        key: "volume.height",
      }),
      new TextField({
        label: "Width(cm)",
        key: "volume.width",
      }), new TextField({
        label: "Depth(cm)",
        key: "volume.depth",
      }),
    );
    let canEdit = true;
    let canDelete = true;
    let actions = {};
    if (canEdit) {
      actions['edit'] = {
        path: '/manage/category/edit/',
        id: 'category_id'
      }
    }
    if (canDelete) {
      actions['delete'] = {
        path: 'category/',
        id: 'category_id'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }

  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    if (this.parentCategoryId) {
      params.parent_category_id = this.parentCategoryId;
    }
    this.categoryService.getCategoryList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {

        this.objectArray = response.data.list;
        this.showPagination = true;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  onClickEvent(obj) {
    if (obj.category_id && obj.parent_category_id) {
      this.categoryName = obj.category_name;
      this.parentCategoryCount = obj.parent_category_id;
      this.parentCategoryId = obj.category_id;
      this.currentPage = 1;
      this.getObjects();
    }
  }

  resetData() {
    this.categoryName = false;
    this.parentCategoryCount = false;
    this.parentCategoryId = false;
    this.getObjects();
  }


  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }
  onReloadEvent() {
    this.getObjects();
  }
}






